<template>
  <div class="house-view">
    <div class="tip">
      <div class="title">南区-楼栋1</div>
    </div>
    <ul class="dothing">
      <li class="item" @click="combineRight">
        <span>向右合并</span>
        <img width="17" height="17" src="ul1.png" alt="">
      </li>
      <li class="item">
        <span>向下合并</span>
        <img width="17" height="17" src="ul2.png" alt="">
      </li>
      <li class="item">
        <span>房间拆分</span>
        <img width="17" height="17" src="ul3.png" alt="">
      </li>
      <li class="item">
        <span>删除列</span>
        <img width="16" height="16" src="ul4.png" alt="">
      </li>
      <li class="item1" @click="edit">
        <span>编辑</span>
        <img width="16" height="16" src="ul5.png" alt="">
      </li>
    </ul>
    <ul class="status">
      <li v-for="item in classMap" track-by="$index">
        <i class="icon" :class="item.class"></i>
        <span>{{item.text}}</span>
      </li>
    </ul>
    <table>
      <thead>
      <tr>
        <th rowspan="2" style="vertical-align: middle;width:105px ">楼层数</th>
        <th style="width:105px ">单元</th>
        <th v-for="item in items" track-by="$index" :colspan="hushuList[$index]">{{item}}单元</th>
      </tr>
      <tr>
        <th style="width:105px ">房号</th>
        <th v-for="item in lists" track-by="$index">{{item}}</th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="ite in louList" track-by="$index">
        <td colspan="2">{{$index+1}}</td>
        <td v-for="item in lists" class="item"  :class="{'active' :i ===1}" track-by="$index" @click="selectImg($event)">{{$parent.$index+1}}{{item}}</td>
      </tr>
      </tbody>
    </table>
    <house-message></house-message>
  </div>
</template>

<script type="text/ecmascript-6">
  import houseMessage from '../../components/houseMessage/houseMessage.vue'

  export default {
    data() {
      return {
        setValue: {
          loucen:3,
          rules:1,
          danyuan:3
        },
        i:'',
        isActive: false,
        hushuList: [2,3,3],
        classMap: [
          {
            class:'green',
            text:'在售'
          },
          {
            class:'red',
            text:'销控'
          },
          {
            class:'yellow',
            text:'认购待审核'
          },
          {
            class:'blue',
            text:'待付款'
          },
          {
            class:'purpos',
            text:'待付款'
          },
          {
            class:'grew',
            text:'付款待确认'
          },
          {
            class:'deepgre',
            text:'待签约'
          },
          {
            class:'black',
            text:'已签约'
          }
        ]
      }
    },
    computed: {
      items() {
        let result = [];
        if(this.setValue.danyuan>10) {
          return;
        }
        for(let i =1;i<=this.setValue.danyuan;i++){
          result.push(i);
        }
        return result;
      },
      louList() {
        let result = [];
        if(this.setValue.loucen>10) {
          return;
        }
        for(let i =1;i<=this.setValue.loucen;i++){
          result.push(i);
        }
        return result;
      },
      lists() {
        let result = [];
        this.hushuList.forEach((i) => {
          for(let a=1;a<= i;a++) {
            result.push(a)
          }
        });
        return result;
      }
    },
    methods: {
      selectImg($event) {
        this.isActive = true;
      },
      combineRight() {

      },
      edit() {

      }
    },
    components: {
      houseMessage
    }
  }
</script>

<style rel="stylesheet/stylus" lang="stylus">
  .house-view
    padding-left 20px
    .tip
      margin-top 25px
      margin-bottom 20px
      .title
        width 138px
        height 36px
        line-height 36px
        text-align center
        background #ff9666
        color #fff
        font-size 14px
        border-radius 5px
    .dothing
      clear both
      overflow hidden
      margin-bottom 25px
      .item
        float left
        box-sizing border-box
        width 125px
        height 35px
        line-height 35px
        background #f6f9fd
        color #616b88
        border 1px solid #d6dfe6
        border-radius 4px
        font-size 14px
        padding-left 10px
        margin-right 16px
        vertical-align middle
        span
          margin-right 30px
        img
          vertical-align middle
      .item1
        float left
        box-sizing border-box
        width 125px
        height 35px
        line-height 35px
        background #46c1de
        color #fff
        border-radius 4px
        font-size 14px
        padding-left 10px
        vertical-align middle
        span
          margin-right 30px
        img
          vertical-align middle
    .status
      overflow hidden
      margin-bottom 25px
      li
        float left
        margin-right 42px
        .icon
          display inline-block
          width 12px
          height 12px
          border-radius 50%
          &.green
            background #0ad27a
          &.red
            background #e47878
          &.yellow
            background #cbc341
          &.blue
            background #46c1de
          &.purpos
            background #b194ec
          &.grew
            background #c5c5c5
          &.deepgre
            background #8d8d8d
          &.black
            background #666666
    table
      width 100%
      .item
        &.active
          background #46c1de
          color #fff
      thead
        width 100%
        tr
          width 100%
          th
            width 428px
      tr ,th ,td
        height 45px
        line-height 45px
        color #616b88
        font-size 14px
        border 1px solid #d6dfe6
        text-align center
      tr,th
        background #f6f9fd
      tbody
        tr:nth-child(odd)
          background #fff

</style>



