<template>
  <div class="housetype">
    <div class="tip">
      <div class="title">调整房间户型</div>
    </div>
    <div class="setting">
      <div class="list-item">
        <img width="22" height="20" src="item1.png" alt="">
        <span>租售类型：</span>
        <span class="list-content">出售</span>
      </div>
      <div class="list-item">
        <img width="20" height="19" src="item2.png" alt="">
        <span>物业类型：</span>
        <span class="list-content">高层</span>
      </div>
      <div class="list-item">
        <img width="20" height="17" src="item3.png" alt="">
        <span>本栋房源总数：</span>
        <span class="list-content">60户</span>
      </div>
      <div class="list-item">
        <img width="20" height="20" src="item4.png" alt="">
        <span>单元数量：</span>
        <span class="list-content">3</span>
      </div>
      <div class="list-item">
        <img width="19" height="17" src="item5.png" alt="">
        <span>楼层数：</span>
        <span class="list-content">3</span>
      </div>
      <div class="list-item-one">
        <div class="wrapper">
          <img width="19" height="18" src="item6.png" alt="">
          <span>楼层数：</span>
        </div>
        <table>
          <tr>
            <td>00户型</td>
            <td>12户型</td>
            <td>test户型</td>
            <td>B户型</td>
          </tr>
        </table>
      </div>
    </div>
    <div class="content">
      <div class="title">
        <img width="20" height="20" src="item7.png" alt="">
        <span>房间户型设置</span>
      </div>
      <table>
        <thead>
          <tr>
            <th rowspan="3">楼层数</th>
            <th>单元</th>
            <th v-for="item in items" track-by="$index" :colspan="hushuList[$index]">{{item}}单元</th>
          </tr>
          <tr>
            <th>房号</th>
            <th v-for="item in lists" track-by="$index">{{item}}</th>
          </tr>
          <tr>
            <th>户型</th>
            <th>
              <select name="" id="1">
                <option value="00">00</option>
              </select>
            </th>
            <th>
              <select name="" id="2">
                <option value="00">00</option>
              </select>
            </th>
            <th>
              <select name="" id="2">
                <option value="00">00</option>
              </select>
            </th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="item in louList" track-by="$index">
            <td>{{$index+1}}</td>
            <td>
              <select name="" id="1">
                <option value="00">00</option>
              </select>
            </td>
            <td v-for="item in lists" track-by="$index">{{$parent.$index+1}}{{item}}</td>
          </tr>
        </tbody>
      </table>
      <div class="bottom">
        <a class="prev" v-link="{path:'/loudong'}">上一步</a>
        <a class="next" v-link="{path:'/success'}">下一步</a>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    data() {
      return {
        setValue: {
          loucen:3,
          rules:1,
          danyuan:3
        },

        hushuList: []
      }
    },
    computed: {
      items() {
        let result = [];
        if(this.setValue.danyuan>10) {
          return;
        }
        for(let i =1;i<=this.setValue.danyuan;i++){
          result.push(i);
        }
        return result;
      },
      louList() {
        let result = [];
        if(this.setValue.loucen>10) {
          return;
        }
        for(let i =1;i<=this.setValue.loucen;i++){
          result.push(i);
        }
        return result;
      },
      lists() {
        let result = [];
        this.hushuList.forEach((i) => {
          for(let a=1;a<= i;a++) {
            result.push(a)
          }
        });
        return result;
      }
    }
  }
</script>

<style rel="stylesheet/stylus" lang="stylus">
  .housetype
    flex 1
    background #f0f4fb
    padding 80px 30px 60px 30px
    .tip
      margin-top 60px
      margin-bottom 20px
      .title
        width 138px
        height 36px
        line-height 36px
        text-align center
        background #ff9666
        color #fff
        font-size 14px
        border-radius 5px
    .setting
      width 1600px
      height 180px
      box-sizing border-box
      padding 26px 80px
      background #fff
      color #616b88
      font-size 16px
      .list-item
        display inline-block
        width 280px
        height 40px
        line-height 40px
        .list-content
          display inline-block
          width 80px
          height 35px
          line-height 35px
          background #f6f9fd
          border 1px solid #d6dfe6
          border-radius 5px
          color #616b88
          font-size 14px
          text-align center
      .list-item-one
        margin-top 25px
        height 60px
        line-height 60px
        .wrapper
          float left
        table
          float left
          width 1350px
          height 45px
          margin-top 7px
          line-height 45px
          background #f6f9fd
          border 1px solid #d6dfe6
          border-radius 5px
          td
            text-align center
            color #616b88
            font-size 14px
    .content
      position relative
      width 1600px
      height 900px
      box-sizing border-box
      margin-top 30px
      background #fff
      color #616b88
      font-size 16px
      padding 25px 50px 130px 50px
      .title
        height 25px
        line-height 25px
        margin-bottom 25px
      table
        width 100%
        thead
          width 100%
          tr
            width 100%
            th
              width 428px
        tr ,th ,td
          height 45px
          line-height 45px
          color #616b88
          font-size 14px
          border 1px solid #d6dfe6
          text-align center
        tr,th
          background #f6f9fd
        tbody
          tr:nth-child(odd)
            background #fff

      .bottom
        position absolute
        left 0
        bottom 40px
        width 100%
        text-align center
        .prev ,.next
          display inline-block
          width 130px
          height 36px
          line-height 36px
          border-radius 5px
          color #fff
          text-align center
        .prev
          background #cccccc
          margin-right 250px
        .next
          background #46c1de
</style>



